import React from 'react';
import style from './app.module.scss';
import {Route, Routes} from "react-router-dom";
import {Header} from "../A1_Header/Header";
import {Home} from "../B0_Home/Home";
import {History} from "../B1_History/History";
import {Chrono} from '../B2_Chrono/Chrono';
import img0 from "../../assets/jpg/home/00.jpg";
import img1 from "../../assets/jpg/home/01.jpg";
import img2 from "../../assets/jpg/home/02.jpg";
import img3 from "../../assets/jpg/home/03.jpg";
import img4 from "../../assets/jpg/home/04.jpg";
import img5 from "../../assets/jpg/home/05.jpg";
import img6 from "../../assets/jpg/home/06.jpg";
import {Photo} from "../B3_Photo/Photo";
import {Bricks} from "../B4_Bricks/Bricks";
import {ByLocation} from "../B4_Bricks/ByLocation/ByLocation";
import {ByMark} from "../B4_Bricks/ByMark/ByMark";
import {Badge} from "../B5_Badge/Badge";
import { Mail } from '../B6_Mail/Mail';
import {BricksLocation} from "../B4_Bricks/BricksLocation/BricksLocation";
import {Mark} from "../B4_Bricks/Mark/Mark";
import {RegistrationPage} from "../A2_RegistrationPage/RegistrationPage";
import {LoginPage} from "../A3_LoginPage/LoginPage";
import {AccountSettingsPage} from "../A4_AccountSettingsPage/AccountSettingsPage";
import {InfoPopup} from "../common/InfoPopup/InfoPopup";
import {BrickIndex} from "../B4_Bricks/BrickIndex/BrickIndex";
import {EditPage} from "../A5_EditPage/EditPage";

export const routes = [
    {
        path: "/",
        element: <Home/>,
        headerLabel: "Главная",
        homeLabel: "Главная",
        img: "",
    },
    {
        path: "/history",
        element: <History/>,
        headerLabel: "История",
        homeLabel: "История",
        img: img0,
    },
    {
        path: "/chrono",
        element: <Chrono/>,
        headerLabel: "Хронология",
        homeLabel: "Хронология",
        img: img1,
    },
    {
        path: "/photo",
        element: <Photo/>,
        headerLabel: "Фото",
        homeLabel: "Фото",
        img: img2,
    },
    {
        path: "/brick",
        element: <Bricks/>,
        headerLabel: "Кирпичи",
        homeLabel: "Кирпичи",
        img: img3,
    },
    {
        path: "/badge",
        element: <Badge/>,
        headerLabel: "Значки",
        homeLabel: "Значки и медали",
        img: img4,
    },
    {
        path: "/mail",
        element: <Mail/>,
        headerLabel: "Конверты",
        homeLabel: "Конверты и открытки",
        img: img5,
    },
    {
        path: "/map",
        element: <Chrono/>,
        headerLabel: "Карта",
        homeLabel: "Интеракивная карта",
        img: img6,
    },
];
export const headerLinks = routes.map(route => ({
    to: route.path,
    headerLabel: route.headerLabel,
}));
export const homeLinks = routes
    .slice(1)
    .map(route => ({
        to: route.path,
        homeLabel: route.homeLabel,
        img: route.img
    }));

export const App = () => {
    return (
        <div className={style.app}>
            <InfoPopup/>
            <Header/>
            <main className={style.main}>
                <Routes>
                    <Route path='/' element={<Home/>}/>
                    <Route path='/registration' element={<RegistrationPage/>}/>
                    <Route path='/login' element={<LoginPage/>}/>
                    <Route path='/account-settings' element={<AccountSettingsPage/>}/>
                    <Route path='/edit' element={<EditPage/>}/>
                    {
                        routes
                            .filter(el => el.path !== "/brick")
                            .map(route => <Route key={route.path} path={route.path} element={route.element}/>)
                    }
                    <Route path="/brick" element={<Bricks/>}>
                        <Route index element={<BrickIndex/>}/>
                        <Route path="location" element={<ByLocation/>}/>
                        <Route path="location/:slug" element={<BricksLocation/>}/>
                        <Route path="mark" element={<ByMark/>}>
                            <Route path=":slug" element={<Mark/>}/>
                        </Route>
                    </Route>

                </Routes>
            </main>
        </div>
    );
};

