import Button from "@mui/material/Button";
import React, {useEffect} from "react";
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import ManageAccountsIcon from '@mui/icons-material/ManageAccounts';
import Popover from "@mui/material/Popover";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import green from "@mui/material/colors/green";
import red from "@mui/material/colors/red";
import {useNavigate} from "react-router";
import {getUserInfo, removeUserInfo} from "../../../localStorage/localStorage";
import IconButton from "@mui/material/IconButton";
import EditIcon from '@mui/icons-material/Edit';
import style from "./auth.module.scss"


const sxListItemIcon = {minWidth: 0, marginRight: "10px"};

export const Auth = () => {
    let navigate = useNavigate();
    const userInfo = getUserInfo();
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const onClickHandler = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget);
    };
    const onCloseHandler = () => {
        setAnchorEl(null);
    };

    const onLoginOpenHandler = () => {
        setAnchorEl(null);
        navigate('/login');
    };

    const onLogoutHandler = () => {
        removeUserInfo();
        setAnchorEl(null);
        navigate('/');
    };

    const onRegisterOpenHandler = () => {
        navigate('/registration');
        setAnchorEl(null);
    };

    const onSettingHandler = () => {
        navigate('/account-settings');
        setAnchorEl(null);
    };

    const onEditDataHandler = () => {
        navigate('/edit');
        setAnchorEl(null);
    }

    return (
        <div>
            <IconButton sx={{color: 'white', textTransform: 'none', marginLeft: "10px"}}
                        aria-controls="simple-menu"
                        aria-haspopup="true"
                        onClick={onClickHandler}
            >
                <AccountCircleIcon sx={{
                    fontSize: "30px",
                    color: userInfo ? green[500] : red[500]
                }}/>
            </IconButton>
            <Popover
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={onCloseHandler}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                {
                    userInfo &&
                    <div className={style.userInfo}>
                        {userInfo.email}
                    </div>
                }
                <List sx={{padding: 0}}
                >
                    {
                        !userInfo &&
                        <ListItem onClick={onLoginOpenHandler}
                                  button
                        >
                            <ListItemIcon sx={sxListItemIcon}>
                                <ExitToAppIcon sx={{color: green[500]}}/>
                            </ListItemIcon>
                            <ListItemText primary='Войти'/>
                        </ListItem>
                    }

                    {
                        !userInfo &&
                        <ListItem onClick={onRegisterOpenHandler}
                                  button
                        >
                            <ListItemIcon sx={sxListItemIcon}>
                                <AccountCircleIcon sx={{color: green[500]}}/>
                            </ListItemIcon>
                            <ListItemText primary='Создать аккаунт'/>
                        </ListItem>
                    }

                    {
                        userInfo &&
                        <ListItem onClick={onEditDataHandler}
                                  button
                        >
                            <ListItemIcon sx={sxListItemIcon}>
                                <EditIcon/>
                            </ListItemIcon>
                            <ListItemText primary='Редактировать'/>
                        </ListItem>
                    }

                    {
                        userInfo &&
                        <ListItem onClick={onSettingHandler}
                                  button
                        >
                            <ListItemIcon sx={sxListItemIcon}>
                                <ManageAccountsIcon/>
                            </ListItemIcon>
                            <ListItemText primary='Настройки профиля'/>
                        </ListItem>
                    }

                    {
                        userInfo &&
                        <ListItem onClick={onLogoutHandler}
                                  button
                        >
                            <ListItemIcon sx={sxListItemIcon}>
                                <ExitToAppIcon sx={{color: red[500]}}/>
                            </ListItemIcon>
                            <ListItemText primary='Выйти'/>
                        </ListItem>
                    }
                </List>
            </Popover>
        </div>
    )
};