import React, {useState} from "react";
import KeyIcon from '@mui/icons-material/Key';
import {Button, TextField} from "@mui/material";
import {FormikErrors, FormikHelpers, useFormik} from "formik";
import {useDispatch, useSelector} from "react-redux";
import style from "./security.module.scss"
import styled from "@mui/material/styles/styled";
import {authAC, selectLoading} from "../../../store/reducers/auth.reducer";
import {appAC} from "../../../store/reducers/app.reducer";
import {authAPI} from "../../../api/auth.api";

export interface IValues {
    oldPassword: string
    newPassword1: string
    newPassword2: string
}

const StyledTextField = styled(TextField)({
    '& p.MuiFormHelperText-root': {
        position: 'absolute',
        left: 0,
        bottom: 0,
        transform: 'translateY(100%)'
    }
});

export const Security = () => {
    const [showForm, setShowForm] = useState(false);
    const isLoading = useSelector(selectLoading);

    const initialValues: IValues = {
        oldPassword: "",
        newPassword1: "",
        newPassword2: "",
    };

    const dispatch = useDispatch();
    const onSubmit = async ({oldPassword, newPassword1, newPassword2}: IValues, formikHelpers: FormikHelpers<IValues>) => {
        try {
            dispatch(authAC.setLoading(true));
            await authAPI.changePassword(oldPassword, newPassword1);
            setShowForm(false);
            dispatch(appAC.showInfoPopup({type: "success", text: "Password successfully changed"}))
        } catch (e: any) {
            formikHelpers.setErrors({
                oldPassword: '  ',
                newPassword1: '  ',
                newPassword2: e.response.data.message
            });
        } finally {
            dispatch(authAC.setLoading(false));
            formikHelpers.setSubmitting(false);
        }
    };

    const validate = ({oldPassword, newPassword1, newPassword2}: IValues): FormikErrors<IValues> => {
        const errors: FormikErrors<IValues> = {};
        if (!oldPassword) {
            errors.oldPassword = 'заполните поле'
        }
        if (!newPassword1) {
            errors.newPassword1 = 'заполните поле'
        }
        if (!newPassword2) {
            errors.newPassword2 = 'заполните поле'
        }
        if (newPassword1 && newPassword2 && newPassword1 !== newPassword2) {
            errors.newPassword2 = 'пароли не совпадают'
        }
        if (newPassword1 && newPassword2 && newPassword1 === newPassword2 && newPassword1 === oldPassword) {
            errors.newPassword2 = 'новый пароль совпадает со старым'
        }
        return errors;
    };

    const formik = useFormik({
        initialValues,
        validate,
        onSubmit
    });

    return (
        <div className={style.security}>
            <Button variant="contained"
                    startIcon={<KeyIcon/>}
                    onClick={() => setShowForm(true)}
            >
                Изменить пароль
            </Button>
            {
                showForm && (
                    <form onSubmit={formik.handleSubmit}
                          className={style.form}
                    >
                        <StyledTextField type="password"
                                         size="small"
                                         fullWidth
                                         variant="outlined"
                                         id="oldPassword"
                                         label="Старый пароль"
                                         error={formik.touched.oldPassword && Boolean(formik.errors.oldPassword)}
                                         helperText={formik.touched.oldPassword && formik.errors.oldPassword}
                                         {...formik.getFieldProps("oldPassword")}
                                         className={style.field}
                        />
                        <StyledTextField type="password"
                                         size="small"
                                         fullWidth
                                         variant="outlined"
                                         id="newPassword1"
                                         label="Новый пароль"
                                         error={formik.touched.newPassword1 && Boolean(formik.errors.newPassword1)}
                                         helperText={formik.touched.newPassword1 && formik.errors.newPassword1}
                                         {...formik.getFieldProps("newPassword1")}
                                         className={style.field}
                        />
                        <StyledTextField type="password"
                                         size="small"
                                         fullWidth
                                         variant="outlined"
                                         id="newPassword2"
                                         label="Повторите новый пароль"
                                         error={formik.touched.newPassword2 && Boolean(formik.errors.newPassword2)}
                                         helperText={formik.touched.newPassword2 && formik.errors.newPassword2}
                                         {...formik.getFieldProps("newPassword2")}
                                         className={style.field}
                        />
                        <Button type='submit'
                                color="primary"
                                fullWidth
                                variant="contained"
                                className={style.field}
                                disabled={formik.isSubmitting || isLoading}
                        >
                            Submit
                        </Button>
                    </form>
                )
            }
        </div>
    )
}