import React, {FC} from "react";
import {IMarkFull} from "../../../types/types";
import style from "./markCard.module.scss"
import clsx from "clsx";
import {BrickCard} from "../BrickCard/BrickCard";

export interface IMarkCard {
    mark: IMarkFull
    className?: string
}

export const MarkCard: FC<IMarkCard> = ({mark, className}) => {
    return (
        <div className={clsx(style.markCard, className)}>

            <p className={style.name}>{mark.name}</p>

            <p className={style.description}>{mark.description}</p>

            <div className={style.brickCards}>
                {
                    mark.bricks.map(brick => <BrickCard key={brick.id} brick={brick}/>)
                }
            </div>

        </div>
    )
}