import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {getBricksLocations, selectBricksLoading, selectBricksLocations} from "../../../store/reducers/bricks.reducer";
import style from "./byLocation.module.scss"
import {Link} from "react-router-dom";

import loc0 from "../../../assets/png/bricks/location0.gif";
import loc1 from "../../../assets/png/bricks/location1.png";
import loc2 from "../../../assets/png/bricks/location2.png";
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';

const imgs = [loc0, loc1, loc2, ''];

export const ByLocation = () => {
    const bricksLocations = useSelector(selectBricksLocations);
    const loading = useSelector(selectBricksLoading);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getBricksLocations());
    }, []);

    return (
        <div className={style.byLocation}>
            {
                bricksLocations.map((loc, index) => (
                        <Link key={loc.id}
                              to={loc.slug}
                              className={style.link}
                              state={{locationId: loc.id}}
                        >
                            <div className={style.imgWrapper}>
                                {
                                    index !== 3
                                    ? <img src={imgs[index]} alt=""/>
                                    : <HelpOutlineIcon sx={{fontSize: "160px", color: "#777"}}/>
                                }

                            </div>
                            <p>
                                {loc.name}
                            </p>
                        </Link>
                    )
                )
            }
        </div>
    )
}