import {BaseThunkType, GetActionsType, StateType} from "../store";
import {authAPI} from "../../api/auth.api";
import {IAvatar} from "../../types/types";


//================ TYPE ==================
export type InitialStateType = typeof initialState;
export type AuthActionsType = GetActionsType<typeof authAC>
type ThunkType = BaseThunkType<AuthActionsType>

//================ INITIAL ==================
const initialState = {
    loading: false,
    nickName: "",
    avatar: null as null | IAvatar,
};

//================ REDUCER ===================
export const authReducer = (state = initialState, action: AuthActionsType): InitialStateType => {
    switch (action.type) {
        case 'GZHATSK/AUTH/SET_LOADING': {
            return {...state, loading: action.loading}
        }
        case 'GZHATSK/AUTH/SET_NICKNAME': {
            return {...state, nickName: action.nickName}
        }
        case 'GZHATSK/AUTH/SET_AVATAR': {
            return {...state, avatar: action.avatar}
        }
        default:
            return state;
    }
};

// ================ ACTION CREATORS =================
export const authAC = {
    setLoading: (loading: boolean) => ({type: 'GZHATSK/AUTH/SET_LOADING', loading} as const),
    setNickName: (nickName: string) => ({type: 'GZHATSK/AUTH/SET_NICKNAME', nickName} as const),
    setAvatar: (avatar: null | IAvatar) => ({type: 'GZHATSK/AUTH/SET_AVATAR', avatar} as const),
};

//================ THUNK CREATORS ==================

//=============== ПОЛУЧИТЬ НИКНЕЙМ ================
export const getNickName = (): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        const response = await authAPI.getNickName();
        dispatch(authAC.setNickName(response.data));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }
};

//=============== ИЗМЕНИТЬ НИКНЕЙМ ================
export const changeNickName = (nickName: string): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        await authAPI.changeNickName(nickName);
        await dispatch(getNickName());
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }
};

//=============== ПОЛУЧИТЬ АВАТАР ================
export const getAvatar = (): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        const response = await authAPI.getAvatar();
        dispatch(authAC.setAvatar(response.data));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }
};

//=============== ИЗМЕНИТЬ АВАТАР ================
export const changeAvatar = (file: File): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        await authAPI.changeAvatar(file);
        await dispatch(getAvatar());
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }


}
//============== SELECTORS =================
export const selectLoading = (state: StateType) => state.auth.loading;
export const selectNickName = (state: StateType) => state.auth.nickName;
export const selectAvatar = (state: StateType) => state.auth.avatar;