import React, {useEffect} from "react";
import {useLocation} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {
    getCurrentLocation,
    selectBricksLoading,
    selectCurrentLocation
} from "../../../store/reducers/bricks.reducer";
import style from "./bricksLocation.module.scss"
import {MarkCard} from "../MarkCard/MarkCard";
import loc0 from "../../../assets/png/bricks/location0.gif";
import loc1 from "../../../assets/png/bricks/location1.png";
import loc2 from "../../../assets/png/bricks/location2.png";
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';

const locationImages = {
    "00000000000000000001": loc0,
    "00000000000000000002": loc1,
    "00000000000000000003": loc2,
}

export const BricksLocation = () => {
    const urlLocation = useLocation();
    console.log(urlLocation);
    // @ts-ignore
    const locationId = urlLocation.state.locationId;

    const currentBricksLocation = useSelector(selectCurrentLocation);
    const loading = useSelector(selectBricksLoading);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getCurrentLocation(locationId));
    }, []);
    // @ts-ignore
    const src = locationImages[locationId];

    // @ts-ignore
    return (
        <div className={style.bricksLocation}>
            {
                !loading && currentBricksLocation &&
                <>
                    <div className={style.nameWrapper}>

                        <div className={style.imgWrapper}>
                            {
                                locationId !== "00000000000000000004"
                                    ? <img src={src} alt=""/>
                                    : <HelpOutlineIcon sx={{fontSize: "40px", color: "#777"}}/>
                            }

                        </div>


                        <p className={style.name}>{currentBricksLocation.name}</p>
                    </div>

                    <div className={style.markCards}>
                        {
                            // @ts-ignore
                            currentBricksLocation.marks.map(mark => <MarkCard key={mark.id} mark={mark}
                                                                              className={style.card}/>)
                        }
                    </div>
                </>
            }

        </div>
    )
}