import {BaseThunkType, GetActionsType, StateType} from "../store";
import {ILocation, ILocationFull, IMarkFull, IMarkItem} from "../../types/types";
import {bricksAPI} from "../../api/bricks.api";

//================ TYPE ==================
export type InitialStateType = typeof initialState;
export type BricksActionsType = GetActionsType<typeof bricksAC>
type ThunkType = BaseThunkType<BricksActionsType>

//================ INITIAL ==================
const initialState = {
    loading: false,
    locations: [] as ILocation[],
    currentLocation: null as null | ILocationFull,
    marks: [] as IMarkItem[],
    currentMark: null as null | IMarkFull,
};

//================ REDUCER ===================
export const bricksReducer = (state = initialState, action: BricksActionsType): InitialStateType => {
    switch (action.type) {
        case 'GZHATSK/BRICKS/SET_LOADING': {
            return {...state, loading: action.loading}
        }
        case 'GZHATSK/BRICKS/SET_LOCATIONS': {
            return {...state, locations: action.locations}
        }
        case 'GZHATSK/BRICKS/SET_CURRENT_LOCATION': {
            return {...state, currentLocation: action.currentLocation}
        }
        case 'GZHATSK/BRICKS/SET_MARKS': {
            return {...state, marks: action.marks}
        }
        case 'GZHATSK/BRICKS/SET_CURRENT_MARK': {
            return {...state, currentMark: action.currentMark}
        }
        default:
            return state;
    }
};

// ================ ACTION CREATORS =================
export const bricksAC = {
    setLoading: (loading: boolean) => ({type: 'GZHATSK/BRICKS/SET_LOADING', loading} as const),
    setLocations: (locations: ILocation[]) => ({type: 'GZHATSK/BRICKS/SET_LOCATIONS', locations} as const),
    setCurrentLocation: (currentLocation: ILocationFull) => ({type: 'GZHATSK/BRICKS/SET_CURRENT_LOCATION', currentLocation} as const),
    setMarks: (marks: IMarkItem[]) => ({type: 'GZHATSK/BRICKS/SET_MARKS', marks} as const),
    setCurrentMark: (currentMark: IMarkFull) => ({type: 'GZHATSK/BRICKS/SET_CURRENT_MARK', currentMark} as const),
}
//================ THUNK CREATORS ==================
export const getBricksLocations = (): ThunkType => async (dispatch) => {
    try {
        dispatch(bricksAC.setLoading(true));
        const response = await bricksAPI.getBricksLocationsList();
        dispatch(bricksAC.setLocations(response.data));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(bricksAC.setLoading(false));
    }
};

export const getCurrentLocation = (id: string): ThunkType => async (dispatch) => {
    try {
        dispatch(bricksAC.setLoading(true));
        const response = await bricksAPI.getBricksLocationById(id);
        dispatch(bricksAC.setCurrentLocation(response.data));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(bricksAC.setLoading(false));
    }
};

export const getAllMarks = (): ThunkType => async (dispatch) => {
    try {
        dispatch(bricksAC.setLoading(true));
        const response = await bricksAPI.getAllMarks();
        dispatch(bricksAC.setMarks(response.data));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(bricksAC.setLoading(false));
    }
};

export const getCurrentMark = (id: string): ThunkType => async (dispatch) => {
    try {
        dispatch(bricksAC.setLoading(true));
        const response = await bricksAPI.getMarkById(id);
        dispatch(bricksAC.setCurrentMark(response.data));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(bricksAC.setLoading(false));
    }
};


//============== SELECTORS =================
export const selectBricksLoading = (state: StateType) => state.bricks.loading;
export const selectBricksLocations = (state: StateType) => state.bricks.locations;
export const selectCurrentLocation = (state: StateType) => state.bricks.currentLocation;
export const selectMarks = (state: StateType) => state.bricks.marks;
export const selectCurrentMark = (state: StateType) => state.bricks.currentMark;