import React, {FC, useState} from "react";
import {IBrick} from "../../../types/types";
import style from "./brickCard.module.scss";
import {Skeleton} from "@mui/material";

interface IBrickCard {
    brick: IBrick
}

export const BrickCard: FC<IBrickCard> = ({brick}) => {
    const value = brick.value + (brick.defectDescription ? ` (${brick.defectDescription})` : '');
    const [loading, setLoading] = useState(true);
    const onLoadHandler = () => setLoading(false);


    return (
        <div className={style.brickCard}>
            <div className={style.imgWrapper}>
                <img src={brick.image.small}
                     alt=""
                     onLoad={onLoadHandler}
                />
                {
                    loading && (
                        <Skeleton variant="rectangular"
                                  sx={{ bgcolor: 'grey.500' }}
                                  width={220}
                                  height={112}
                                  className={style.skeleton}
                        />
                    )
                }
            </div>
            <p>{value}</p>
        </div>
    )
}