import React, {useEffect} from "react";
import {useLocation} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {getCurrentMark, selectBricksLoading, selectCurrentMark} from "../../../store/reducers/bricks.reducer";
import style from "./mark.module.scss";
import {MarkCard} from "../MarkCard/MarkCard";

export const Mark = () => {
    const location = useLocation();
    // @ts-ignore
    const markId = location.state.id;

    const loading = useSelector(selectBricksLoading);
    const mark = useSelector(selectCurrentMark);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getCurrentMark(markId));
    }, [dispatch, markId]);

    return (
        <>
            {
                !loading && mark && (
                        <MarkCard mark={mark} className={style.mark}/>
                )
            }
        </>
    )
}