import React, {useEffect} from "react";
import style from "./byMark.module.scss"
import {useDispatch, useSelector} from "react-redux";
import {
    getAllMarks,
    selectBricksLoading,
    selectMarks
} from "../../../store/reducers/bricks.reducer";
import {NavLink, Outlet} from "react-router-dom";
import clsx from "clsx";

export const ByMark = () => {
    const marks = useSelector(selectMarks);
    const loading = useSelector(selectBricksLoading);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getAllMarks());
    }, []);



    return (
        <div className={style.byMark}>
            <div className={style.links}>
                {
                    marks.map(mark => (
                        <NavLink to={`${mark.slug}`}
                                 className={style.link}
                                 key={mark.slug}
                                 state={{id: mark.id}}
                        >
                            {
                                ({ isActive }) => (
                                    <div className={clsx({
                                        [style.linkInner]: true,
                                        [style.linkInner_active]: isActive,
                                    })}>
                                        {mark.name}
                                    </div>
                                )
                            }

                        </NavLink>
                    ))
                }
            </div>

            <div className={style.outlet}>
                <Outlet/>
            </div>

        </div>
    )
}