import React from "react";
import style from "./bricks.module.scss";
import brick1 from "../../assets/png/bricks/bricks1.png";
import brick2 from "../../assets/png/bricks/bricks2.png";
import {NavLink, Outlet} from "react-router-dom";
import clsx from "clsx";

export const Bricks = () => {

    return (
        <section className={style.bricks}>
            <div className={style.inner}>
                <div className={style.titleBlock}>
                    <div className={style.left}>
                        <img src={brick1} alt=""/>
                    </div>

                    <h1 className={style.title}>Дореволюционные кирпичи</h1>

                    <div className={style.right}>
                        <img src={brick2} alt=""/>
                    </div>

                </div>

                <div className={style.sortMethod}>
                    <p>Сортировка по:</p>
                    <div className={style.links}>
                        <NavLink to='location'>
                            {
                                ({isActive}) => (
                                    <span className={clsx({
                                        [style.link]: true,
                                        [style.link_active]: isActive,
                                    })}>По месту</span>
                                )
                            }
                        </NavLink>
                        <NavLink to='mark'>
                            {
                                ({isActive}) => (
                                    <span className={clsx({
                                        [style.link]: true,
                                        [style.link_active]: isActive,
                                    })}>По клейму</span>
                                )
                            }
                        </NavLink>
                    </div>
                </div>

                <div className={style.outlet}>
                    <Outlet/>
                </div>

            </div>

        </section>
    )
}